import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePageView from '../views/HomePageView.vue'

const HomeView = () => import('@/views/HomeView.vue')
const EnglishView = () => import('@/views/English/HomeView.vue')

const HomeChildren = () => import('@/views/Layout/Home/index.vue')

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: HomePageView,
        meta: {title: '山河大学社科部 - Shanhe Social science'}
    },
    {
        path: '/home',
        component: HomeView,
        meta: {title: '首页 - 山河大学社科部'},
        children: [
            {
                path: '/home',
                component: HomeChildren,
                meta: {title: '首页 - 山河大学社科部'}
            },
        ]
    },
    {
        path: '/english',
        component: EnglishView,
        meta: {title: 'Home Page - Shanhe Social science'}
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {//beforeEach是router的钩子函数，在进入路由前执行
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next();
})

export default router
